.anim{
    position: relative;
    top:40px;
  left:10px;
  font-size: 2rem;
   animation: slideUp 2s linear .5s infinite alternate;
   opacity: 0;
}
@keyframes slideUp {  
    0% {transform: translateY(0);}
    20%{color:rgb(51, 0, 255)}
    100% {transform: translateY(-40px);opacity:1; color:rgb(247, 203, 8)}
  }
  
  